import styled from 'styled-components'

export default styled.input`
  display: block;
  width: 100%;
  height: 3rem;
  padding-left: .5rem;
  color: white;
  font-size: 1.25rem;
  background: #333;
  border: none;
  border-top: .15rem solid white;
`



